                            <p className="tracking-widest opacity-80 mb-12 italic">"{rawName}" ë‹˜ì˜ ìµœì¢… í˜•ìƒì´ íˆ¬ê³¼ë˜ì—ˆìŠµë‹ˆë‹¤.</p>

